<template>
  <div class="xa-webkit-vertical xa-flex">
    <div class="xa-flex">
      <xa-input v-model="password" label="" position="top" type="text" placeholder=""></xa-input>
      <xa-input v-model="new_password" label="" position="top" type="text" placeholder=""></xa-input>
      <xa-input v-model="re_password" label="" position="top" type="text" placeholder=""></xa-input>
    </div>
    <div style="padding:10px;">
      <xa-button @onclick="onBtnClick" ripple="rgba(255,255,255,0.3)" :radius="6" width="100%" class="xa-bg-red xa-txt-white" event="onclick"></xa-button>
    </div>
  </div>
</template>

<script>
import { setDocumentTitle } from 'utils'
import { modifyPassword } from 'service/member'

export default {
  data () {
    return {
      password: '',
      new_password: '',
      re_password: ''
    }
  },
  methods: {
    onBtnClick () {
      if (!this.password.trim()) {
        return this.$toast.show('', 3000)
      }
      if (!this.new_password.trim()) {
        return this.$toast.show('', 3000)
      }
      if (!this.re_password.trim()) {
        return this.$toast.show('', 3000)
      }
      if (this.new_password.trim().length < 6 || this.new_password.trim().length > 16) {
        return this.$toast.show('6-16', 3000)
      }
      if (this.new_password.trim() !== this.re_password.trim()) {
        return this.$toast.show('', 3000)
      }
      this.$toast.show('...')
      modifyPassword({password: this.password.trim(), new_password: this.new_password.trim()}).then(data => {
        this.$toast.show('', 3000)
        this.$router.back()
      }).catch(() => {
        this.$toast.show('', 3000)
      })
    }
  },
  created () {
    setDocumentTitle('')
  }
}
</script>



