<template>
  <div>
    <div class="form-title"></div>
    <div class="app-lines" style="margin-bottom:0;">
      <a href="http://mjixiang.github.io/demo/d3_tree/" style="color:black;" class="app-line xa-webkit-box">
        <circel-icon icon="icon-zuzhijiegoutu" color="#44BBFC"></circel-icon>
        <div class="app-line-text xa-flex">1</div>
        <div class="xa-arrow xa-webkit-center"></div>
      </a>
      <div class="app-line xa-webkit-box">
        <circel-icon color="#F7BF28"></circel-icon>
        <div class="app-line-text xa-flex">2</div>
        <div class="xa-arrow xa-webkit-center"></div>
      </div>
    </div>
    <div class="form-title"></div>
    <div class="app-lines">
      <div @click="onMemberClick" class="app-line xa-webkit-box">
        <circel-icon color="#F8796F"></circel-icon>
        <div class="app-line-hd xa-flex">
          <div class="app-line-title"></div>
          <div class="app-line-subtitle">CEO</div>
        </div>
      </div>
      <div @click="onMemberClick" class="app-line xa-webkit-box">
        <circel-icon color="#F8796F"></circel-icon>
        <div class="app-line-hd xa-flex">
          <div class="app-line-title"></div>
          <div class="app-line-subtitle">2</div>
        </div>
      </div>
      <div @click="onMemberClick" class="app-line xa-webkit-box">
        <circel-icon color="#F8796F"></circel-icon>
        <div class="app-line-hd xa-flex">
          <div class="app-line-title"></div>
          <div class="app-line-subtitle"></div>
        </div>
      </div>
    </div>
    <div class="app-lines">
      <div @click="onMemberClick" v-for="i in 10" :key="i" class="app-line xa-webkit-box">
        <circel-icon color="#F08CB6"></circel-icon>
        <div class="app-line-hd xa-flex">
          <div class="app-line-title"></div>
          <div class="app-line-subtitle"></div>
        </div>
      </div>
    </div>

    <xa-popup v-model="isShowPopup" from="bottom">
      <div class="form-title"></div>
      <xa-cells>
        <xa-cell right title="" desc=""></xa-cell>
        <xa-cell right title="" desc=""></xa-cell>
        <xa-cell right title="" desc="20"></xa-cell>
        <xa-cell right title="" desc="wx_number"></xa-cell>
        <xa-cell right title="" desc="13580316195"></xa-cell>
        <xa-cell right title="" desc=""></xa-cell>
        <xa-cell right title="" desc="13580316195"></xa-cell>
      </xa-cells>
    </xa-popup>
  </div>
</template>

<script>
import circelIcon from 'components/circelIcon.vue'
import { setDocumentTitle } from 'utils'

export default {
  components: {circelIcon},
  data () {
    return {
      isShowPopup: false
    }
  },
  methods: {
    onMemberClick () {
      this.isShowPopup = true
    }
  },
  created () {
    setDocumentTitle('')
  }
}
</script>

<style scoped>
  .app-lines{margin-bottom: 9px;}
  .app-line{padding: 8px 14px;background-color:white;border-bottom: 1px solid #f2f2f2;}
  .app-line-text{padding: 8px 14px; font-size: 16px;}
  .app-line-hd{padding: 4px 14px;}
  .app-line-title{line-height:16px;color:#333;}
  .app-line-subtitle{line-height:16px;color:#888;font-size:10px;}
</style>




